<template>
  <div class="project">
    <button v-on:click=close($event) > </button>
    <h1> {{projectName}} </h1>  
    <a id="courseLink" :href = "courseLink" > <h3>{{ course }}</h3> </a>
    
    <!-- images -->
    <vueper-slides v-if="!!images" :touchable="false" fixed-height="35vh">
      <vueper-slide v-for="image in images" :key="image"
        :image="image"
        :style="{'background-size': 'contain',
                 'background-repeat': 'no-repeat'}"/>
    </vueper-slides>

    <div v-else-if="images === ''" id="special_images"></div>

    <h5>{{ description }}</h5>

    <div id="linkContainer">
      <a v-if="!!repo" :href = "repo" > <p> source code </p> </a>
      <a v-if="!!report" :href = "report" > <p> report </p> </a>
    </div>

    <!-- tool logos -->
    <ul id="logos"  >
      <li v-for="(link, lang) in languages" :key="lang"
      :style=" { 'height' : '5vw', 'width' : '5vw',
                'background-image': 'url(' + link + ')',
                'background-repeat': 'no-repeat',
                'background-position': 'center',
                'background-size': 'contain' }" > 
      </li>  
    </ul>

  </div>
</template>

<script>
  import {VueperSlides, VueperSlide} from 'vueperslides'
  import 'vueperslides/dist/vueperslides.css'

  export default {
    name: 'Project',
    components:{
      VueperSlides, VueperSlide
    },
    props: {
      projectName: String,
      course: String,
      courseLink: String,
      images: Array,            // list of urls
      description: String,
      repo: String,             // url
      report: String,           // url
      languages: Object
    },
    data(){
      return {}
    },
    methods: {

      // on:click close - hide Project component
      close(event){
        let projectID = event.currentTarget.parentElement.id;
        //console.log('clicked on close for ', projectID)
        let clickedProject = document.getElementById(projectID);
        clickedProject.style = "display: none; z-index: -1";

      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  /* project components */
  /* all objects with id starting with 'project' */
  [id^=project]{

    /* to lay on top */
    position: fixed;  
    left: 5%;
    top: 3%;
    width: 90%;
    max-height: 95%;
    cursor: default;

    /* border */
    border-width: 1px;
    border-color: LightGray;
    border-style: solid;
    border-radius: 8px;
    background-color: white;

    /* vertical content */
    flex-direction: column;
    justify-content: space-between;

    overflow: scroll;
  }
  
  /* project name */
  h1 {  
    width: 100%;
    margin-top: 5vw;
    margin-bottom: auto;
    font-size: min(4vw, 36px);
  }

  #courseLink {
    margin-bottom: 30px;
    font:1em/1.2 'Clarendon', 'ITC Bookman', 'Bookman Old Style', 'URW Bookman L', 'Sentinel', serif;
    font-size-adjust:.5;
    font-size: min(2.5vw, 19px);
    font-style: italic;
  }

  #special_images {
    width: 100%;
    height: 400px;
    margin:auto;
    
  }
  /* Tama */    /* horizontal% vertical%, topLeft: 0,0, bottomRight: 100,100; */
  #project1 > #special_images {
    background-image: 
    url("https://cdn3.cdnme.se/2438591/9-3/eating_5f92e5faddf2b3195ab77c61.gif"),
    url("https://cdn2.cdnme.se/2438591/9-3/apple_5f92e5f92a6b228500fa8251.gif"),
    url("https://cdn1.cdnme.se/2438591/9-3/happy_5f92e5fc9606ee1540f2f30f.gif"), 
    url("https://cdn2.cdnme.se/2438591/9-3/tama_5f92dc799606ee14fe558e3a.gif"), 
    url("https://cdn2.cdnme.se/2438591/9-3/sad_5f92e5ffe087c31fa823fc75.gif"),
    url("https://cdn2.cdnme.se/2438591/9-3/poop_5f92e5fdddf2b3194afb518d.gif");
    background-position: 0% 50%, 20% 60%, 35% 50%, 60% 50%, 85% 50%, 95% 60%;
    background-size: 15vw 15vw, 5vw 5vw, 15vw 15vw, 15vw 15vw, 15vw 15vw, 5vw 5vw, 15vw 15vw;
    background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
  }

  /* source code and/or report */
  #linkContainer {
    width: 100%;
    margin-bottom: auto;
    display: inline-flex;
    justify-content: center;
    flex-wrap: nowrap;
  }

  a {
    margin-left: 30px;
    margin-right: 30px;
    font:1em/1.22 'Calibri', 'Candara', 'Carlito', 'Segoe', 'Segoe UI', 'Optima', 'Nimbus Sans L', 'Selawik', sans-serif;
    font-size-adjust:.47; 
    font-variant:lining-nums;
    font-size: max(14px, min(2vw, 17px));
    color: #FF99FF;/*#42b983;*/
  }

  a:hover {
    color: #FFE6FF;
    font-style: italic;
  }

  /* project description */
  h5 {
    max-width: 95%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
    font:1em/1.22 'Calibri', 'Candara', 'Carlito', 'Segoe', 'Segoe UI', 'Optima', 'Nimbus Sans L', 'Selawik', sans-serif;
    font-size-adjust:.47; 
    font-variant:lining-nums;
    font-size: max(16px, min(2vw, 19px));
  }

  
  /* close button */
  button {
    position: absolute;
    align-self: flex-end;
    height: 45px;
    width: 45px;
    margin: 5px 5px 0 0;
    cursor: pointer;

    background-color: white;
    border:0;
    
    background-image: url("https://cdn2.cdnme.se/2438591/9-3/close_5f92d8809606ee152fe5ca4d.png");
    background-size: cover;
    
  }

  /* list of logos */
  #logos {
    height: 5vw;
    width: 100%;
    background-color: #FFE6FF;

    /* bottom of Project component */
    position: relative;
    padding: 0;
    margin-top: auto;

    /* no bullets */
    list-style-type: none;
    
    /* horizontal logos */
    display: inline-flex;
    justify-content: space-around;
    flex-wrap: nowrap;
  }

</style>
