<template>
  <div id="app" v-on:click=closeProject($event)>

    <div id="menu">
      <h1 class="menuItem"> about </h1>
      <h1 class="menuItem"> contact </h1>
    </div>

    <div id="projectsContainer">

      <!-- For each project: previews -->
      <div class="projects" v-for="project in projects" :key="project.id" 
        :style="{'background-image': 'url(' + project.image + ')', 'background-repeat': 'no-repeat',
        'background-position': 'center',
        'background-size': 'cover'}"
        v-on:click=click($event) >

        <div id="banners">
          <h1 id="projName"> {{project.name}} </h1>  

          <!-- tool logos -->
          <ul id="logos"  class="banners">
            <li v-for="(link, lang) in project.languages" :key = "lang"
            :style=" { 'height' : 'max(2.5vw, 30px)', 
                      'width' : 'max(2.5vw, 30px)',
                      'background-image': 'url(' + link + ')',
                      'background-repeat': 'no-repeat',
                      'background-position': 'center',
                      'background-size': 'contain' }" > 
            </li>  
          </ul> 
        </div>

        <!-- pop-up component -->
        <Project  :projectName = "project.name"
                  :course = "project.course"
                  :courseLink = "project.courseLink"
                  :images = "project.more_images"
                  :description = "project.description" 
                  :repo = "project.repo"
                  :report = "project.report"
                  :languages = "project.languages"

                  :id="'project' + project.id"
                  :style="{'display': 'none'}"/>

      </div>

    </div>

  </div>
</template>

<script>
  // load json-data  
  import projectsData from '@/projects.json'
  import Project from '@/components/Project.vue'

  export default {
    name: 'App',
    components:{
      Project
    },
    data(){
      return {
        projects: projectsData // json-data
      }
    },
    methods:{

      // on:click preview - show Project component
      click(event) { 
        // get Project component of clicked preview
        let projectID = event.currentTarget.children[1].id;
        let clickedProject = document.getElementById(projectID);

        // show Project component only if it was not just closed
        let zIndex = clickedProject.style.zIndex;
        if (zIndex != -1) {
          clickedProject.style = "display: flex; z-index: 5";
          //console.log('clicked on preview of ', projectID)
        } 
        else clickedProject.style = "display: none; z-index: 0";

      },

      // on:click app div - hide current showing Project component
      closeProject(event){
     
        // get element on top of click pos
        let topElement = event.path[0].id;

        // if click was outside any preview/project component
        if (topElement === 'projectsContainer' || 
            topElement === 'menu'){

          // find open Project component to hide it
          let projects = document.querySelectorAll('[id^="project"]');
          for (let i = 1; i < projects.length; i++){
            let style = projects[i].style.display;
            
            // hide the showing Project component
            if (style !== 'none')
              projects[i].style.display = "none";
            
          }
        }
      }

    }
  }
</script>

